/** @define media */

/*
* This file contains all styles related to the table element.
*/

table {
	@apply w-full text-left;
	margin-top: var(--leading);
	border-collapse: collapse;
	border-spacing: 0;
}

thead tr {
	@apply border-t border-b border-ubc-blue;
}

tr {
	@apply border-b border-grey-300;
}

td,
th {
	@apply p-4;
	line-height: 1.2;
	vertical-align: top;
}

th {
	@apply font-semibold text-ubc-blue;
}

/* table element variants */
.table--responsive {
	max-width: 100%;
	margin: 0;
	overflow-x: auto;
}

.table--plain tr {
	@apply border-b-0;
	background-color: transparent !important;
}

.table--condensed th,
.table--condensed td {
	@apply py-2 pr-2;
	line-height: 1.25;
}

.table--striped tr.odd,
.table--striped tbody tr:nth-child(2n+1) {
	background-color: $table-row-striping-color;
}

.table--striped td,
.table--striped th {
	padding-left: .75rem;
}

.table--hover tbody tr:hover {
	background-color: $table-row-hover-color;
}
