/*
* This file contains all default application-wide Sass and CSS3 variables.
*/

/* used to determine when to trigger an external icon */
$site-url: "e.ubc.ca";

/* Type Variables */

$text-font-stack: Catamaran, Arial, sans-serif;
$serif-font-stack: Merriweather, Georgia, Cambria, 'Times New Roman', Times, serif;
$code-font-stack: Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;

$default-typeface: $text-font-stack;
$alternate-typeface: $serif-font-stack;

/* Type Sizes */
$base-line-height: var(--leading);

$text-xs: .75rem; /* 12px */
$text-sm: .875rem; /* 14px */
$text-base: 1rem; /* 16px */
$text-lg: 1.125rem; /* 18px */
$text-xl: 1.25rem; /* 20px */
$text-2xl: 1.5rem; /* 24px */
$text-3xl: 1.75rem; /* 28px */
$text-4xl: 2rem; /* 32px */
$text-5xl: 2.25rem; /* 36px */
$text-6xl: 2.75rem; /* 44px */
$text-7xl: 3.375rem; /* 54px */
$text-8xl: 4rem; /* 64px */
$text-9xl: 5rem; /* 80px */
$text-10xl: 7.625rem; /* 122px */

$text-12: $text-xs; /* 12px */
$text-14: $text-sm; /* 14px */
$text-16: $text-base; /* 16px */
$text-18: $text-lg; /* 18px */
$text-20: $text-xl; /* 20px */
$text-24: $text-2xl; /* 24px */
$text-28: $text-3xl; /* 28px */
$text-32: $text-4xl; /* 32px */
$text-36: $text-5xl; /* 36px */
$text-44: $text-6xl; /* 44px */
$text-54: $text-7xl; /* 54px */
$text-64: $text-8xl; /* 64px */
$text-80: $text-9xl; /* 80px */
$text-122: $text-10xl; /* 122px */

/* Colors */
$transparent: transparent;
$black: #000;
$white: #fff;

$ubc-blue: #002145; /* rgb - (0,33,69) */
$ubc-blue-sea: #003a79; /* rgb - (0,58,121) */
$ubc-blue-cobalt: #0055b7; /* rgb - (0,85,183) */
$ubc-blue-neptune: #40b4e5; /* rgb - (64,180,229) */
$ubc-blue-sky: #61c1e9; /* rgb - (110,196,232) */
$ubc-blue-cornflower: #97d4e9; /* rgb - (151,212,233) */
$ubc-blue-polar: #def1f8; /* rgb - (255,243,249)  (was #e1f3f9) */

$unit-primary: var(--color-primary);
$unit-secondary: var(--color-secondary);
$unit-tertiary: var(--color-tertiary);
$unit-tertiary-alt: var(--color-tertiary-alt);
$unit-accent: var(--color-accent);
$grey-50: #fafafa;
$grey-100: #f5f5f5;
$grey-300: #e0e0e0;
$grey-500: #9e9e9e;
$grey-700: #616161;
$grey-900: #212121;

$error: #ff7c7c;
$warning: #f8e605;
$success: #57edae;
$status: $ubc-blue-neptune;

$white-transparent: rgba(255, 255, 255, .5);
$white--translucent: rgba(255, 255, 255, .2);
$ubc-blue--transparent: rgba(0, 33, 69, .9);
$ubc-blue--tint: rgba(0, 33, 69, .25);
$gradient-ubc-blue-transparent: rgba(0, 33, 69, 0);
$gradient-ubc-blue-opaque: rgba(0, 33, 69, 1);
$gradient-grey-transparent: rgba(244, 244, 244, 0);
$gradient-grey-opaque: rgba(244, 244, 244, 1);

/* Default colors */
$default-text-color: $grey-900;
$default-link-color: $ubc-blue-cobalt;
$default-link-color-hover: $ubc-blue;
$default-link-color-active: $ubc-blue;

$box-shadow-color: rgba(0, 0, 0, .1);
$image-overlay-bg-colour: rgba(69, 69, 69, 1);
$image-overlay-screen-colour: rgba(125, 134, 140, .2);

/* Title weights */
$h1-weight: 600;
$h2-weight: 600;
$h3-weight: 600;
$h4-weight: 600;
$h5-weight: 600;
$h6-weight: 600;

/* Forms */
$input-border-radius: 0;
$input-text: $grey-700;
$input-border: $grey-300;
$input-bg: $white;
$input-disabled-color: $grey-100;
$input-disabled-bg: $grey-700;
$input-disabled-border: $grey-700;
$input-select-border: $grey-300;
$input-select-bg: $white;
$input-select-text: $grey-700;
$input-check-radio-color: $ubc-blue;
$input-check-radio-border: $ubc-blue;
$input-placeholder-color: $grey-300;
$input-required-color: $error;
$focus-bg: rgba(64, 180, 229, .5);
$focus-inner-bg: rgba(0, 33, 69, 1);
$focus-required-bg: rgba(255, 124, 124, .5);
$focus-required-inner-bg: #d62f2f;

/* Buttons */
$gradient-black-transparent: rgba(0, 0, 0, 0);
$gradient-black-translucent: rgba(0, 0, 0, .2);
$gradient-black-opaque: rgba(0, 0, 0, 1);
$button-text: $white;
$button-bg: $ubc-blue;
$button-border: $transparent;
$button-text--hover: $white;
$button-bg--hover: $ubc-blue-sea;
$button-border--hover: $transparent;
$button-text--active: $white;
$button-bg--active: $ubc-blue-sea;
$button-border--active: $transparent;
$button-light-bg: var(--color-tertiary);
$button-light-border: $transparent;
$button-light-text: $black;
$button-light-bg--hover: var(--color-tertiary-alt);
$button-light-border--hover: $transparent;
$button-light-text--hover: $black;
$button-light-bg--active: var(--color-tertiary-alt);
$button-light-border--active: $transparent;
$button-light-text--active: $black;
$button-dark-bg: $ubc-blue;
$button-dark-border: $transparent;
$button-dark-text: $white;
$button-dark-bg--hover: $ubc-blue-cobalt;
$button-dark-border--hover: $transparent;
$button-dark-text--hover: $white;
$button-dark-bg--active: $ubc-blue-cobalt;
$button-dark-border--active: $transparent;
$button-dark-text--active: $white;
$button-transparent-bg: rgba(255, 255, 255, .2);
$button-transparent-border: $transparent;
$button-transparent-text: $white;
$button-transparent-bg--hover: rgba(255, 255, 255, .4);
$button-transparent-border--hover: $transparent;
$button-transparent-text--hover: $white;
$button-transparent-bg--active: rgba(255, 255, 255, .5);
$button-transparent-border--active: $transparent;
$button-transparent-text--active: $white;

/* Tables */
$table-row-striping-color: rgba(225, 243, 249, .5);
$table-row-hover-color: rgba(225, 243, 249, .5);

/* Carousel */
$carousel-button--disabled: $grey-500;
$carousel-item--focus-outline: $ubc-blue-cobalt;
$carousel-item--focus-border: $white;
$carousel-caption--text-shadow: 0 1px 2px rgba(0, 0, 0, .6);
$carousel-gradient-transparent: rgba(0, 0, 0, .0001);
$carousel-gradient-translucent: rgba(0, 0, 0, .7);

/* Throbber / ajax loading animation */
$throbber-background: rgba(255, 255, 255, .5);
$throbber-border: rgba(51, 51, 51, .25);
$throbber-color: $unit-primary;

/* CLF 8 Breakpoints map */
$breakpoint--xs: 480px;
$breakpoint--sm: 768px;
$breakpoint--md: 980px;
$breakpoint--lg: 1200px;
$breakpoint--xl: 1720px;
$breakpoint--xxl: 2400px;

$breakpoint--xs--max: 479px;
$breakpoint--sm--max: 767px;
$breakpoint--md--max: 979px;
$breakpoint--lg--max: 1199px;
$breakpoint--xl--max: 1719px;
$breakpoint--xxl--max: 2399px;

/* CLF 7 Breakpoints map to control version 7 of the CLF separately */
$breakpoint--clf7-xs: 360px;
$breakpoint--clf7-sm: 768px;
$breakpoint--clf7-md: 980px;
$breakpoint--clf7-lg: 1200px;
$breakpoint--clf7-xl: $breakpoint--xl;
$breakpoint--clf7-xxl: $breakpoint--xxl;

$breakpoint--clf7-xs--max: 359px;
$breakpoint--clf7-sm--max: 767px;
$breakpoint--clf7-md--max: 979px;
$breakpoint--clf7-lg--max: 1199px;
$breakpoint--clf7-xl--max: $breakpoint--xl--max;
$breakpoint--clf7-xxl--max: $breakpoint--xxl--max;
